import React, { useEffect, useState } from 'react';
import { GeoJSON } from 'react-leaflet';

const GEOSERVER_URL =
  'http://localhost:8080/geoserver/RMUTSV/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=RMUTSV%3ALand&maxFeatures=50&outputFormat=application%2Fjson&srsName=EPSG:4326';


const Land = ({ selectedFeature, setSelectedFeature, onGeojsonLoaded, searchField = '' }) => {
    const [geojson, setGeojson] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [hoveredFeatureId, setHoveredFeatureId] = useState(null);

    useEffect(() => {
        setLoading(true);
        fetch(GEOSERVER_URL)
            .then((res) => {
                if (!res.ok) throw new Error('Network response was not ok');
                return res.json();
            })
            .then((data) => {
                setGeojson(data);
                setLoading(false);
                if (onGeojsonLoaded) {
                    onGeojsonLoaded(data);
                }
            })
            .catch((err) => {
                setError(err.message);
                setLoading(false);
            });
    }, []);


    const onEachFeature = (feature, layer) => {
        //  field  tooltip
        const value = feature.properties[searchField];
        const tooltipText = (value !== undefined && value !== null && value !== '') ? String(value) : "";
        layer.bindTooltip(tooltipText, { direction: 'top', permanent: true, className: 'building-label' });

        layer.on('click', () => {
            setSelectedFeature(feature);
        });
        layer.on('mouseover', () => {
            setHoveredFeatureId(feature.id || feature.properties['']);
        });
        layer.on('mouseout', () => {
            setHoveredFeatureId(null);
        });
    };


    //  feature 
    const isSelected = (feature) => {
        if (!selectedFeature) return false;
        return feature.id === selectedFeature.id || feature.properties[''] === selectedFeature.properties[''];
    };

    //  feature  hover 
    const isHovered = (feature) => {
        if (!hoveredFeatureId) return false;
        return (feature.id || feature.properties['']) === hoveredFeatureId;
    };

    //  selectedFeature 
    useEffect(() => {
        if (!geojson) return;
        // trigger re-render  key
        setGeojson({ ...geojson });
    }, [selectedFeature]);

    if (loading) return <div>Loading Land data...</div>;
    if (error) return <div style={{color:'red'}}>Error loading Land data: {error}</div>;
    if (!geojson) return null;

    return (
        <GeoJSON
            key={selectedFeature ? selectedFeature.id || selectedFeature.properties[''] : 'none'}
            data={geojson}
            style={feature => {
                if (isSelected(feature)) {
                    return {
                        weight: 1,
                        color: 'black',
                        fillColor: 'yellow',
                        fillOpacity: 1,
                        zIndex: 9999,
                    };
                } else if (isHovered(feature)) {
                    return {
                        weight: 1,
                        color: 'black',
                        fillColor: 'yellow',
                        fillOpacity: 1,
                        zIndex: 9999,
                    };
                } else {
                    return {
                        weight: 1,
                        color: 'black',
                        fillColor: 'pink',
                        fillOpacity: 1,
                        zIndex: 9999,
                    };
                }
            }}
            onEachFeature={onEachFeature}
        />
    );
};

export default Land;