import React, { useEffect, useState } from 'react';
import { GeoJSON } from 'react-leaflet';

const GEOSERVER_URL =
  'http://localhost:8080/geoserver/RMUTSV/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=RMUTSV%3ABoundary&maxFeatures=50&outputFormat=application%2Fjson&srsName=EPSG:4326';

const Boundary = ({ onSelectFeature }) => {
    const [geojson, setGeojson] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        setLoading(true);
        fetch(GEOSERVER_URL)
            .then((res) => {
                if (!res.ok) throw new Error('Network response was not ok');
                return res.json();
            })
            .then((data) => {
                setGeojson(data);
                setLoading(false);
            })
            .catch((err) => {
                setError(err.message);
                setLoading(false);
            });
    }, []);

    const onEachFeature = (feature, layer) => {
        if (onSelectFeature) {
            layer.on('click', () => onSelectFeature(feature));
        }
    };

    if (loading) return <div>Loading Boundary data...</div>;
    if (error) return <div style={{color:'red'}}>Error loading Boundary data: {error}</div>;
    if (!geojson) return null;

    return (
        <GeoJSON
            data={geojson}
            style={{
                weight: 2,
                color: 'blue',
                fillOpacity: 0,
                zIndex: 6000,
            }}
            onEachFeature={onEachFeature}
        />
    );
};

export default Boundary;
