import React, { useEffect } from 'react';
import { useMap } from 'react-leaflet';
import L from 'leaflet';

const MapLegend = () => {
    const map = useMap();

    useEffect(() => {
        const legend = L.control({ position: 'bottomleft' });

        legend.onAdd = () => {
            const div = L.DomUtil.create('div', 'legend');
            div.innerHTML = `
                <div style="background: rgba(255, 255, 255, 0.8); padding: 10px; border-radius: 5px;">
                    <p><strong></strong></p>
                    <div style="display: flex; align-items: center; gap: 5px;">
                        <div style="width: 15px; height: 15px; background: pink; border: 1px solid #aaa;"></div> 
                    </div>
                    <div style="display: flex; align-items: center; gap: 5px; margin-top: 4px;">
                        <div style="width: 15px; height: 3px; background: blue; border: 1px solid #aaa;"></div> 
                    </div>
                </div>
            `;
            return div;
        };

        legend.addTo(map);
        return () => map.removeControl(legend);
    }, [map]);

    return null;
};

export default MapLegend;