import React from 'react';
import html2canvas from 'html2canvas'; // 

const MapPopup = ({ feature, onClose }) => {
    if (!feature) return null;

    const { properties } = feature;
    const popupContent = Object.entries(properties)
        .map(([key, value]) => {
            if (key === ' (Degree)mean') {
                value = parseFloat(value).toFixed(3);
            }
            return `<strong>${key}:</strong> ${value || ''}`;
        })
        .join('<br/>');

    //  popup
    const handleSaveImage = () => {
        const popupElement = document.getElementById('popup-print-content');
        html2canvas(popupElement).then(canvas => {
            const link = document.createElement('a');
            link.download = 'popup.png';
            link.href = canvas.toDataURL('image/png');
            link.click();
        });
    };

    return (
        <div style={{
            position: 'absolute',
            bottom: '20px',
            right: '20px',
            background: 'white',
            padding: '10px',
            borderRadius: '5px',
            boxShadow: '0 2px 10px rgba(0,0,0,0.2)',
            zIndex: 9999,
            maxWidth: '250px'
        }}>
            <button onClick={onClose} style={{
                position: 'absolute',
                top: '5px',
                right: '5px',
                background: 'transparent',
                border: 'none',
                fontSize: '18px',
                cursor: 'pointer',
                width: '28px',
                height: '28px',
                lineHeight: '24px',
                textAlign: 'center'
            }}></button>
            <div
                id="popup-print-content"
                style={{
                    margin: 7.5,
                    padding: 7.5,
                    background: 'white',
                    borderRadius: 0
                }}
                dangerouslySetInnerHTML={{ __html: popupContent }}
            />
        </div>
    );
};

export default MapPopup;
