import React, { useState, useEffect } from 'react';

const GEOSERVER_URL =
  'http://localhost:8080/geoserver/RMUTSV/ows?service=WFS&version=1.0.0&request=GetFeature&typeName=RMUTSV%3ALand&maxFeatures=50&outputFormat=application%2Fjson&srsName=EPSG:4326';

const SEARCH_FIELDS = [
    { label: '', value: '' },
    { label: '', value: '' },
    //  field 
];

const LandList = ({ onSelectFeature }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredData, setFilteredData] = useState([]);
    const [allData, setAllData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [searchField, setSearchField] = useState('');

    useEffect(() => {
        setLoading(true);
        fetch(GEOSERVER_URL)
            .then((res) => {
                if (!res.ok) throw new Error('Network response was not ok');
                return res.json();
            })
            .then((data) => {
                setAllData(data.features || []);
                setLoading(false);
            })
            .catch((err) => {
                setError(err.message);
                setLoading(false);
            });
    }, []);

    useEffect(() => {
        if (searchTerm.trim() === '') {
            setFilteredData([]);
            return;
        }
        const results = allData.filter((feature) =>
            feature.properties[searchField]?.toString().includes(searchTerm)
        );
        setFilteredData(results);
    }, [searchTerm, allData, searchField]);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const handleSearchFieldChange = (event) => {
        setSearchField(event.target.value);
        setSearchTerm('');
        setFilteredData([]);
    };

    const handleFilter = () => {
        setSearchTerm(searchTerm.trim());
    };

    const handleKeyDown = (event) => {
        if (event.key === 'Enter') {
            handleFilter();
        }
    };

    const handleSelect = (feature) => {
        onSelectFeature(feature);
        setSearchTerm('');
        setFilteredData([]);
    };

    if (loading) return <div>Loading Land list...</div>;
    if (error) return <div style={{color:'red'}}>Error loading Land list: {error}</div>;

    return (
        <div style={{
            width: '100%',
            height: '100%',
            padding: '10px',
            fontSize: '12px',
            fontWeight: 'normal',
        }}>
            <h3 style={{
                fontSize: '16px',
                fontWeight: 'normal',
                color: 'black',
                lineHeight: '0'
            }}
            ></h3>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', marginBottom: '10px', gap: '8px' }}>
                <select
                    value={searchField}
                    onChange={handleSearchFieldChange}
                    style={{
                        height: '40px',
                        borderRadius: '5px',
                        border: '1px solid #ccc',
                        fontSize: '14px',
                        padding: '0 8px',
                        boxSizing: 'border-box'
                    }}
                >
                    {SEARCH_FIELDS.map(field => (
                        <option key={field.value} value={field.value}>{field.label}</option>
                    ))}
                </select>
                <input
                    type="text"
                    placeholder={`${SEARCH_FIELDS.find(f => f.value === searchField)?.label || ''}...`}
                    value={searchTerm}
                    onChange={handleSearchChange}
                    onKeyDown={handleKeyDown}
                    style={{
                        width: '60%',
                        padding: '8px',
                        fontWeight: 'normal',
                        borderRadius: '5px',
                        border: '1px solid #ccc',
                        height: '40px',
                        boxSizing: 'border-box'
                    }}
                />
            </div>
            {filteredData.length > 0 && (
                <ul style={{
                    listStyle: 'none',
                    padding: '5px',
                    maxHeight: '325px',
                    overflowY: 'auto',
                    border: '1px solid #ccc',
                    borderRadius: '5px',
                    background: '#fff'
                }}>
                    {filteredData.map((feature, index) => (
                        <li key={index}
                            onClick={() => handleSelect(feature)}
                            style={{
                                padding: '8px',
                                cursor: 'pointer',
                                borderBottom: '1px solid #ddd',
                                textAlign: 'left'
                            }}>
                            {SEARCH_FIELDS.map(field => (
                                <span key={field.value} style={{ marginRight: '8px' }}>
                                    <strong>{field.label}:</strong> {feature.properties[field.value]}
                                </span>
                            ))}
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
};

export default LandList;
