import React, { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Marker, useMap } from 'react-leaflet';
import L from 'leaflet';
import proj4 from 'proj4';

import LandList from './LandList';
import LayerControl from './LayerControl';
import MapLegend from './MapLegend';
import MapPopup from './MapPopup';
import MapZoomToFeature from './MapZoomToFeature';
import './map.css';

import customIconUrl from './images/Marker.png';

const CustomIcon = L.icon({
    iconUrl: customIconUrl,
    iconSize: [50, 50], // 
    iconAnchor: [25, 50] //  anchor 
});

const MapContents = () => {
    const [selectedFeature, setSelectedFeature] = useState(null);
    const [isSidebarVisible, setIsSidebarVisible] = useState(true);
    const [landGeojson, setLandGeojson] = useState(null);
    const [searchField, setSearchField] = useState('');
    //  geojson  Land.js
    const handleLandGeojsonLoaded = (geojson) => {
        setLandGeojson(geojson);
    };

    //  fitBounds
    const MapFitBoundsToLand = ({ geojson }) => {
        const map = useMap();
        useEffect(() => {
            if (geojson) {
                const L = require('leaflet');
                const bounds = L.geoJSON(geojson).getBounds();
                if (bounds.isValid()) {
                    map.fitBounds(bounds, { padding: [20, 20] });
                }
            }
        }, [geojson, map]);
        return null;
    };


    useEffect(() => {
        document.title = "";
    }, []);

    const handleMapClick = (e) => {
        setSelectedFeature(null);
    };

    const handleClosePopup = () => {
        setSelectedFeature(null);
    };

    // Fix: Add handleSelectFeature and toggleSidebar
    const handleSelectFeature = (feature) => {
        setSelectedFeature(feature);
    };

    const toggleSidebar = () => {
        setIsSidebarVisible((prev) => !prev);
    };

    return (
        <div style={{ display: 'flex', height: '100vh', backgroundColor: 'white' }}>
            {isSidebarVisible && (
                <div style={{
                    flex: 1,
                    padding: '10px',
                    fontSize: '18px',
                    textAlign: 'center',
                    lineHeight: '0',
                    position: 'relative'
                }}>
                    <p style={{ lineHeight: '1' }}>
                        <img src={`${process.env.PUBLIC_URL}/assets/logo.png`} alt="Logo" style={{ height: '60px' }} />
                    </p>
                    <p style={{ fontSize: '24px', color: 'black', lineHeight: '1.6' }}>
                        < br />
                    </p>
                    <p style={{ lineHeight: '1.6' }}>
                        <LandList onSelectFeature={handleSelectFeature} onSearchFieldChange={setSearchField} />
                    </p>
                    <button onClick={toggleSidebar} style={{
                        position: 'absolute',
                        zIndex: 1000,
                        top: '50%',
                        right: '-20px',
                        transform: 'translateY(-50%)',
                        backgroundColor: 'white',
                        color: 'black',
                        border: '1px solid black',
                        borderRadius: '5px',
                        padding: '10px',
                        cursor: 'pointer'
                    }}>
                        {'<'}
                    </button>
                </div>
            )}

            <div style={{ flex: isSidebarVisible ? 3 : 1 }}>
                {!isSidebarVisible && (
                    <button onClick={toggleSidebar} style={{
                        position: 'absolute',
                        zIndex: 1000,
                        top: '50%',
                        left: '10px',
                        transform: 'translateY(-50%)',
                        backgroundColor: 'white',
                        color: 'black',
                        border: '1px solid black',
                        borderRadius: '5px',
                        padding: '10px',
                        cursor: 'pointer'
                    }}>
                        {'>'}
                    </button>
                )}
                <MapContainer
                    style={{ width: '100%', height: '100%' }}
                    center={[7.206, 100.595]} // fallback center
                    zoom={10} // fallback zoom
                    scrollWheelZoom={true}
                    onClick={handleMapClick}
                >
                    {/* Fit map to Land extent when loaded */}
                    {landGeojson && <MapFitBoundsToLand geojson={landGeojson} />}
                    <TileLayer
                        attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    <LayerControl selectedFeature={selectedFeature} setSelectedFeature={setSelectedFeature} onLandGeojsonLoaded={handleLandGeojsonLoaded} searchField={searchField} />
                    <MapZoomToFeature feature={selectedFeature} />
                    <MapLegend />
                    {selectedFeature && <MapPopup feature={selectedFeature} onClose={handleClosePopup} />}
                </MapContainer>
            </div>
        </div>
    );
};

export default MapContents;